@import url(https://fonts.googleapis.com/css?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{font-family:'VT323', monospace;display:flex;justify-content:center;height:100vh;width:100vw;background:radial-gradient(ellipse at center, #000 50%, #003D02 100%);border-radius:20px;box-shadow:inset 0px 0px 15px 15px #000;background-size:100% 5px;background-repeat:repeat-y}.screen{margin-top:100px}.screen .outPut_timer{padding-top:25px;font-size:60px;color:#01a301;text-shadow:0px 0px 10px grey;opacity:0.6;margin-left:25px;margin-right:25px}.screen .button__item{width:120px;padding-top:2px;padding-bottom:2px;margin-right:10px;margin-top:15px;border-radius:8px;box-shadow:inset 0px 0px 20px 0px rgba(250,250,250,0.3);border:0;font-family:'VT323', monospace;color:rgba(3,185,3,0.726);font-size:22px;cursor:pointer;text-transform:uppercase;border:1px solid #777777af;background:#303030;outline:none}.screen .button__item:hover{background-color:#9e9d9da4;text-shadow:0px 0px 10px grey}

